@charset "utf-8";

/**
 * ==   Pages > Resume Edit
 */

.resume-sidebar {
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.12);
  float: left;
  width: 16rem;

  @media all and (max-width: 992px) {
    display: none;
  }
}

.resume-sidebar .resume-sidebar-header {
  background-color: #1c84c6;
  padding: .5rem;
}

.resume-sidebar .resume-sidebar-header font {
  color: #fff;
  font-size: .9375rem;
  padding-left: .5rem;
}

.resume-sidebar-header .resume-menu {
  background-color: #75B9E7;
  border: 1px solid #aed0e4;
  border-radius: 4px;
  cursor: pointer;
  padding: 1rem 1.25rem;
  z-index: 999;
}

.resume-sidebar-header .resume-menu:hover {
  opacity: 0.9;
}

.resume-menu span {
  background-color: #fff;
  display: block;
  position: absolute;
  left: calc(50% - .8125rem);
  top: calc(50% - 1px);
  height: 2px;
  width: 1.625rem;
}

.resume-menu span:before,
.resume-menu span:after {
  content: '';
  background-color: #fff;
  display: block;
  height: 2px;
  position: absolute;
  width: 1.625rem;
}

.resume-menu span:before {
  top: 7px;
}

.resume-menu span:after {
  top: -7px;
}

.resume-sidebar.sticky {
  top: 0;
}

.resume-box {
  background-color: #fff;
  border-bottom: 2px solid #1c84c6;
  padding: .75rem .5rem .5rem;
}

.resume-box .edit_input {}

.resume-box .resume-status .btn {
  border-radius: 4px;
  font-size: .8125rem;
  padding: .375rem;
}

.resume-box .resume-status {
  margin: .5rem 0;
}

.menu-list-resume {
  background-color: #f0f3f5;
}

.menu-list-resume .menu-title {
  font-size: .8125rem;
  padding: .5rem 1rem;
}

.menu-list-resume .label--unfinish,
.menu-list-resume .label--open,
.menu-list-resume li .toggle {
  position: absolute;
  right: 4px;
  top: 50%;
  transform: translateY(-50%);
}

.menu-list-resume li {
  background-color: #fff;
  margin-left: .25rem;
  margin-right: .25rem;
  padding: .125rem 0;
  user-select: none;
}

.menu-list-resume li:hover {
  background-color: #eef4f9;
}

.menu-list-resume li a {
  color: #B1B6BC;
  display: block;
  font-size: 0;
  line-height: 2.25rem;
  padding-left: 1rem;
  height: 2.25rem;
}

.menu-list-resume li:hover a {
  color: #6C757D;
}

.menu-list-resume li.active a {
  color: #3d3d3d;
  font-weight: bolder;
}

.resume-icon {
  background-color: #DAE1E5;
  border-radius: 50%;
  font-size: 1.25rem;
  margin: 0 1rem 0 0;
  height: 2rem;
  width: 2rem;
}

.menu-list-resume li.active:nth-child(-n+5).active [class^="resume-icon"],
.menu-list-resume li.active:nth-child(-n+5) [class*="resume-icon"] {
  background-color: #B7E2F6;
}

.menu-list-resume li.active:nth-child(-n+5) [class^="resume-icon"] i,
.menu-list-resume li.active:nth-child(-n+5) [class*="resume-icon"] i {
  color: #1c84c6;
}

.menu-list-resume li.active:nth-child(n+6).active [class^="resume-icon"],
.menu-list-resume li.active:nth-child(n+6) [class*="resume-icon"] {
  background-color: #caefe8;
}

.menu-list-resume li.active:nth-child(n+6) [class^="resume-icon"] i,
.menu-list-resume li.active:nth-child(n+6) [class*="resume-icon"] i {
  color: #1abc9c;
}

.menu-list-resume li.active:nth-of-type(9).active [class^="resume-icon"],
.menu-list-resume li.active:nth-of-type(9) [class*="resume-icon"] {
  background-color: #ffd6de;
}

.menu-list-resume lli.active:nth-of-type(9) [class^="resume-icon"] i,
.menu-list-resume li.active:nth-of-type(9) [class*="resume-icon"] i {
  color: #ff6b81;
}

.menu-list-resume li.active:nth-of-type(n+10).active [class^="resume-icon"],
.menu-list-resume li.active:nth-of-type(n+10) [class*="resume-icon"] {
  background-color: #fce1d8;
}

.menu-list-resume li.active:nth-of-type(n+10) [class^="resume-icon"] i,
.menu-list-resume li.active:nth-of-type(n+10) [class*="resume-icon"] i {
  color: #ff7f50;
}

.menu-list-resume .dashe-li {
  background-color: rgb(243, 243, 243);
  border-radius: 2px;
  border: 1px #ccc dashed;
}

.menu-list-resume .dashe-li:hover {
  background-color: rgb(253, 253, 253);
}

.menu-list-resume .time-update {
  color: #b1b6bc;
  font-size: .75rem;
  text-align: right;
  padding: .5rem;
}

.resume_block .btn {
  border-radius: 4px;
}

