  width: calc(100% - 7rem);
}

.resume-panel .resume-panel-title {
  background: #f5f6fa;
  border-top: 1px solid #EBEBEB;
  border-bottom: 1px solid #EBEBEB;
  padding: .5rem 1rem;
}

.resume-panel-title .title {
  font-size: 1rem;
  font-weight: bold;
}

.resume-panel-title .btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  /* width: 12rem; */
