}

.resume-edit-title .btn {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
}

.resume-panel .resume-panel-main {
  padding: 1rem;
}

.resume-panel-main .profile-item-img {
  width: 25%;
}

.resume-panel-main .profile-item-text {
  width: 75%;
}

.resume-adv {
  background-image: url("../img/resume-ani.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 13.5rem;
}

.resume-separator {
  background: url('data:image/svg+xml,<svg version="1.1" xmlns="http://www.w3.org/2000/svg" width="9px" height="6px"><polygon opacity="0.2" points="4.5,4.5 0,0 0,1.208 4.5,5.708 9,1.208 9,0 "/></svg>') 0 50% repeat-x;
  margin: 2.5rem 0 .5rem;
  text-align: center;
}

.resume-separator h3 {
  background: #dde3e8;
  display: inline-block;
  font-size: 1.5rem;
  letter-spacing: .5px;
  padding: 0 1rem;
}

.resume-separator:before,
.resume-separator:after {
  position: absolute;
  left: 50%;
  color: #68BBE3;
  letter-spacing: 4px;
  transform: translateX(-50%);
  font-family: 'JobBank' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-feature-settings: "liga";
  -moz-font-feature-settings: "liga=1";
  -moz-font-feature-settings: "liga";
  -ms-font-feature-settings: "liga" 1;
  font-feature-settings: "liga";
  -webkit-font-variant-ligatures: discretionary-ligatures;
  font-variant-ligatures: discretionary-ligatures;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.resume-separator:before {
  content: '\e93a \e93a \e93a \e93a \e93a';
  font-size: .875rem;
  top: -1rem;
}

.resume-separator:after {
  content: '\e93a';
  font-size: 1.25rem;
  top: -1.25rem;
}

.resume-panel-main .profile-left-box,
.resume-panel-main .profile-right-box {
  padding: .25rem 0 .25rem 1rem;
  width: 50%;
  vertical-align: top;
}

@media all and (max-width: 768px) {
  .resume-panel-main .profile-left-box,
  .resume-panel-main .profile-right-box {
    width: 100%;
  }
}

.resume-panel-main .profile-left-box {
  border-right: 1px solid #efefef;
}

