}

/* list-card */
.resume-edit-content .list-card {
  background-color: #FDFEFF;
  border: #efefef 1px solid;
  border-radius: 4px;
  box-shadow: 0 2px 1px 0px rgba(0, 0, 0, 0.1);
  margin-bottom: 1.25rem;
  padding: .75rem 1rem 1.5rem 1.5rem;
}

.list-card-header {
  padding: .5rem 0;
}

.list-card .item-action-btn {
  position: absolute;
  right: 0;
  top: 0;
}

.list-card-content {}

/* Academic */
.list-card.academic_card {
  border-left: #7CC1E4 3px solid;
}

.academic_card:before {
  position: absolute;
  left: -3.5rem;
  top: 50%;
  transform: translateY(-50%);
  height: 2.25rem;
  width: 2.25rem;
  background-color: #75b9e7;
  border-radius: 50%;
  color: #fff;
  font-size: 1.25rem;
  line-height: 1.75;
  text-align: center;
  z-index: 2;
}

.academic_card:nth-of-type(1):before {
  content: '1';
}

.academic_card:nth-of-type(2):before {
  content: '2';
}

.academic_card:nth-of-type(3):before {
  content: '3';
}

.academic_card:nth-of-type(4):before {
  content: '4';
}

.academic_card:after {
  content: '';
  position: absolute;
  left: -2.45rem;
  background-color: #fff;
  height: 50%;
  width: 5px;
  z-index: 1;
}

.academic_card:first-of-type:after {
  top: -1px;
}

.academic_card:last-of-type:after {
  bottom: -1px;
}

