.slider-bar-radio {
  background: #dde3ee;
  border-radius: 12px;
  font-size: 0;
  height: 12px;
  width: 100%;
  margin: 1rem 0;
  text-align: center;
}

.slider-bar-radio input[type=radio] {
  display: none;
}

.slider-bar-radio label {
  cursor: pointer;
  display: inline-block;
  font-size: .8125rem;
  height: 4rem;
  line-height: 1.5rem;
  margin: 1.75rem 0 0 0;
  text-align: center;
}

.slider-bar-radio.language label {
  width: 25%;
}

.slider-bar-radio.dialect label {
  width: 33.333333%;
}

.slider-bar-radio label:after {
  content: '';
  background: #dde3ee;
  border-radius: 50%;
  box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.16);
  display: block;
  position: absolute;
  left: 1.625rem;
  top: -2rem;
  height: 1.25rem;
  width: 1.25rem;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
}

.slider-bar-radio input[type="radio"]:checked + label {
  color: #1c84c6;
  font-weight: bold;
}

.slider-bar-radio input[type="radio"]:checked + label:after {
  background: #1c84c6;
  height: 1.5rem;
  width: 1.5rem;
  top: -2.125rem;
}

.slider-bar-radio input[type="radio"] + label:before {
  content: '';
  background: #b1d0e9;
  border-radius: 12px;
  height: 12px;
  width: 0;
  position: absolute;
  left: 0;
  top: -1.75rem;
  opacity: 0;
}

.slider-bar-radio input[type="radio"]:checked + label:before {
  opacity: 1;
}

.slider-bar-radio input[type="radio"]:nth-of-type(1):checked + label:before {
  left: 0;
  width: 50%;
}

.slider-bar-radio input[type="radio"]:nth-of-type(2):checked + label:before {
  left: -100%;
  width: 150%;
}

.slider-bar-radio.language input[type="radio"]:nth-of-type(3):checked + label:before {
  left: -200%;
  width: 250%;
}

.slider-bar-radio.language input[type="radio"]:nth-of-type(4):checked + label:before {
  left: -300%;
  width: 400%;
}

.slider-bar-radio.dialect input[type="radio"]:nth-of-type(3):checked + label:before {
  left: -200%;
  width: 300%;
}

