}

.magic-ctrl,
.magic-bubble {
  display: inline-block;
}

.magic-ctrl {
  vertical-align: top;
  width: 50%;
}

.magic-bubble {
  border-radius: 8px;
  font-size: .875rem;
  padding: .75rem 1rem;
  text-align: left;
  z-index: 1;
}

.magic-ctrl .magic-bubble.bubble--left {
  background-color: #1098AD;
  color: #fff;
