    margin-right: 1rem;
    margin-left: 2rem;
  }
}

.magic-ctrl .bubble--left:before,
.magic-ctrl .bubble--right:before {
  content: '';
  position: absolute;
  bottom: -.75rem;
  height: 0;
  width: 0;
  border: 1.5rem solid transparent;
  border-bottom: none;
  z-index: -1;
}

.magic-ctrl .bubble--left:before {
  border-top-color: #1098AD;
  right: 1rem;
  transform: matrix(-1, 1, 1, 0, 0, 0);
}

.magic-ctrl .bubble--right:before {
  border-top-color: #cef4f7;
  left: 1rem;
  transform: matrix(-1, -1, -1, 0, 0, 0);
