/* magic loading */
.magic-img-peoples img {
  height: 8rem;
  width: 20rem;
  animation: blink infinite ease-in-out 1.25s alternate;
}

.loading-text {
  padding: 1.5rem 0 1rem;
  animation: blink infinite ease-in-out 1s alternate;
}

@keyframes blink {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.loading,
.loading:after {
  border-radius: 2rem;
  height: 2rem;
}

.loading {
  background: #E6E7EB;
  width: 50%;
  margin: 0 auto .5rem;
}

.loading:after {
  content: '';
  background-image: repeating-linear-gradient(-45deg, #65CDE0, #65CDE0 20px, #52A6B2 20px, #52A6B2 30px);
  position: absolute;
  width: 100%;
  z-index: 1;
  animation: loading 5s linear infinite;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#65cde0",endColorstr="#52a6b2",GradientType=1);
}

@keyframes loading {
  0% {
    width: 0;
  }

  70% {
    opacity: 1;
    width: 100%;
  }

  90% {
    opacity: 0;
    width: 100%;
  }

  100% {
    opacity: 0;
    width: 0;
  }
}

